@import "./../../styles/design";

.songContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 55vh;
}

.cover {
  width: 280px;
  max-width: 60%;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px -5px $c-mine-shaft;
}

.title {
  padding: 2rem 1rem 1rem;
}

.artist {
  font-size: 1rem;
}

@include desktop {
  .songContainer {
    min-height: 60vh;
  }
  .cover {
    max-width: 35%;
  }
}