@import "./../../styles/design";

.player {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 20vh;
}

.timeControl {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  max-width: 90%;
  margin: 1rem 0 1rem;
}

.track {
  position: relative;
  width: 100%;
  height: 1rem;
  margin: 0 1rem;
  background-size: 100% 100%;
  border-radius: 1rem;
  overflow: hidden;
  &:hover {
    .animateTrack {
      opacity: 0.7;
    }
  }
}

.sliderBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-color: $c-mine-shaft;
  filter: blur(6px);
  pointer-events: none;
  transform: scale(1.4);
}

.timeInput {
  width: 100%;
  appearance: none;
  background: transparent;
  outline: none;
  cursor: pointer;
  &::focus {
    outline: none;
  }
  &::-webkit-slider-thumb {
    height: 16px;
    width: 16px;
  }
}

.animateTrack {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $c-silver;
  transform: translateX(0%);
  pointer-events: none;
  opacity: 1;
  transition: opacity .3s ease-out;
}

.playerControl {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  max-width: 70%;
  padding: 1rem;
}

.icon {
  cursor: pointer;
  color: $c-mine-shaft;
  opacity: 0.8;
  transition: opacity .3s ease-out;
  &:hover {
    opacity: 1;
  }
}

.timestamp {
  font-family: Arial, Helvetica, sans-serif;
  color: $c-mine-shaft;
}

.volumeContainer {
  position: absolute;
  right: -11rem;
  bottom: 1.5rem;
  display: none;
  flex-direction: column-reverse;
  height: 10rem;
  overflow: hidden;
  &:hover {
    .volumeTrack {
      opacity: 1;
      transform: translateY(0%);
    }
  }
}

.volumeTrack {
  position: absolute;
  right: 0;
  bottom: 2rem;
  left: 0;
  width: 1rem;
  background-size: 100% 100%;
  border-radius: 1rem;
  overflow: hidden;
  transform: translateY(-100%);
  opacity: 0;
  transition: all .3s ease-out;
  &:hover {
    .volumeAnimateTrack {
      opacity: 0.7;
    }
  }
}

.volumeSliderBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-color: $c-silver;
  pointer-events: none;
}

.volumeInput {
  width: 1rem;
  height: 7rem;
  appearance: none;
  background: transparent;
  outline: none;
  cursor: pointer;
  -webkit-appearance: slider-vertical;
  writing-mode: bt-lr;
  &::focus {
    outline: none;
  }
  &::-webkit-slider-thumb {
    height: 16px;
    width: 16px;
  }
}

.volumeAnimateTrack {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $c-eastern-snow-gradient;
  transform: translateY(100%);
  pointer-events: none;
  opacity: 1;
  transition: opacity .3s ease-out;
}

.volumeIconContainer {
  display: block;
  width: 36px;
}

@include desktop {
  .volumeContainer {
    display: flex;
  }
}